body {
  margin: -40vmin 0 0 -40vmin;
  position: fixed;
  top: 50%;
  left: 50%;
}

.watch {
  background-color: #b3afaf;
  border: 10px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch:before {
  content: "";
  z-index: 1;
  background-color: orange;
  border-radius: 50%;
  width: 6%;
  height: 6%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clock {
  border-radius: 30%;
}

.clock__hand--hour {
  background-color: #00f;
  width: 3%;
  height: 25%;
  animation: 60s linear infinite move;
}

.clock__hand--minute {
  background-color: green;
  width: 2%;
  height: 50%;
  animation: 5s linear infinite move;
}

.clock__hand--minute, .clock__hand--hour {
  transform-origin: top;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes move {
  0% {
    transform: rotate(-180deg);
  }

  100% {
    transform: rotate(180deg);
  }
}

